.m_figure {

  #m_prose > #default > .spacing();
  margin-left: unit(-1 * @gutter/@base,rem);
  margin-right: unit(-1 * @gutter/@base,rem);
  #breakpoint > .med({
    margin: 0;//reset the browser
  });

  &__caption {
    display: block;
    color: @c_grey;
    text-align: center;
    #m_prose > #default > .size(@max: 14; @min: 12;);
    #m_prose > #default > .spacing(@top: .25; );
    margin-left: unit(@gutter/@base,rem);
    margin-right: unit(@gutter/@base,rem);
  }

  &__captionNumber {
    color: lighten(@c_grey,20%);
  }

  &__captionNumber + &__captionNumber {
    margin-left: unit(@gutter/4/@base,rem);
  }

  &__image {
    width: 100%;
    display: block;
  }

  &__item + &__item > &__image {
    margin-top: 1px;
    #breakpoint > .med({ margin-top: 0;});
  }

  @med_pull: unit(@bp_prose/@base,rem);

  @wideish_image: 810;
  @wideish_pull: unit((-1 * ((@wideish_image - @bp_prose) / 2))/@base,rem);

  @wide_image: 1000;
  @wide_pull: unit((-1 * ((@wide_image - @bp_prose) / 2))/@base,rem);

  @extra_wide_image: 1600;
  @extra_wide_pull: unit((-1 * ((@extra_wide_image - @bp_prose) / 2))/@base,rem);

  //poke, pull and span
  &--poke-both
  &--pull-both,
  &--span-both {
    #breakpoint > .med({
      margin-left: ~"calc(-100vw / 2 + @{med_pull} / 2)";
      margin-right: ~"calc(-100vw / 2 + @{med_pull} / 2)";
    });
  }

  &--poke-both {
    @media screen and (min-width: unit(@wideish_image,px)) {
      margin-left: @wideish_pull;
      margin-right: @wideish_pull;
    }
  }

  &--pull-both {
    @media screen and (min-width: unit(@wide_image,px)) {
      margin-left: @wide_pull;
      margin-right: @wide_pull;
    }
  }

  &--span-both {
    @media screen and (min-width: unit(@extra_wide_image,px)) {
      margin-left: @extra_wide_pull;
      margin-right: @extra_wide_pull;
    }
  }

  &--pull-left &__image,
  &--pull-right &__image {
    width: auto;
    margin: auto;
  }

  &--pull-left &__caption,
  &--pull-right &__caption {
    margin-bottom: 0;
  }

  &--pull-left {
    #breakpoint > .med({
      margin-left: ~"calc(-100vw / 2 + @{med_pull} / 2)";
      margin-right: unit(@gutter/@base,rem);
      float: left;
      max-width: 50%;
      padding-top: 10px; //align with top of text
    });
    @media screen and (min-width: unit(@wide_image,px)) {
      margin-left: @wide_pull;
      max-width: 63%;
    }
  }

  &--pull-right {
    width: auto;
    #breakpoint > .med({
      margin-right: ~"calc(-100vw / 2 + @{med_pull} / 2)";
      margin-left: unit(@gutter/@base,rem);
      float: right;
      max-width: 50%;
      padding-top: 10px; //align with top of text
    });
    @media screen and (min-width: unit(@wide_image,px)) {
      margin-right: @wide_pull;
      max-width: 63%;
    }
  }

  //galleries
  &__gallery {
    #breakpoint > .med({
      display: flex;
      .m_figure__item {
        margin-right: unit(@gutter/2/@base,rem);
      }
      .m_figure__item:last-of-type {
        margin-right: 0;
      }
    });
  }

}
